.to-top{
position: fixed;
    z-index: 1;
    top: 80%;
    left: 85%;

    background-color: rgba(238, 229, 229, 0.503);

    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
   
    
    @media screen and (max-width: 479px) {
        border-radius: 10px;
        padding: 10px 10px 7px 10px;
        
        
    }
    
    @media screen and (min-width: 480px) { 
        border-radius: 10px;
        padding: 17px 10px 2px 10px;
        
        
    }
}

.btn-to-top {

    background-color: rgba(238, 229, 229, 0);

    display: flex;
    align-items: center;
    justify-content: center;
    display: none;
    
    border-radius: 4px;
    
    border: solid #ff6b01;
    display: inline-block;
    
   
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
    
    @media screen and (max-width: 479px) {
        width: 15px;
        height:15px;
        border-width: 0 3px 3px 0;
        }

    @media screen and (min-width: 480px) {
        width: 30px;
        height: 30px;
        border-width: 0 6px 6px 0;
    }
}

.to-top:hover {
    
    cursor: pointer;
    transform: scale(1.3);
    box-shadow: 5px 5px 3px 0px rgba(122, 118, 118, 0.3);
}

