.loader__square {
  width: calc(var(--f) * 2);
  height: calc(var(--f) * 2);
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  transform: rotate(45deg);
}

.loader__square-item {
  width: calc(var(--f)  - 4px);
  height: calc(var(--f)  - 4px);
  border: 1px solid #fff;
  border-radius: 20%;
  box-shadow: inset 0 10px 27px -8px #141414, inset 0 -10px 27px -8px #a31925, 5px 5px 15px 5px rgba(0, 0, 0, 0);
}

.loader__square-item:nth-child(1) {
  background-color: #6495ed;
  animation: square-step-1 2s linear infinite;
}

.loader__square-item:nth-child(2) {
  background-color: violet;
  animation: square-step-2 2s linear infinite;
}

.loader__square-item:nth-child(3) {
  background-color: #9acd32;
  animation: square-step-3 2s linear infinite;
}

@keyframes square-step-2 {
  0% {
    transform: translate(0%);
  }

  8.3% {
    transform: translate(0%, 100%);
  }

  24.9% {
    transform: translate(0%, 100%);
  }

  33.2% {
    transform: translate(-100%, 100%);
  }

  49.8% {
    transform: translate(-100%, 100%);
  }

  58.1% {
    transform: translate(-100%);
  }

  74.7% {
    transform: translate(-100%);
  }

  83% {
    transform: translate(0%);
  }
}

@keyframes square-step-1 {
  8.3% {
    transform: translate(0%);
  }

  16.6% {
    transform: translate(100%);
  }

  33.2% {
    transform: translate(100%);
  }

  41.5% {
    transform: translate(100%, 100%);
  }

  58.1% {
    transform: translate(100%, 100%);
  }

  66.4% {
    transform: translate(0%, 100%);
  }

  83% {
    transform: translate(0%, 100%);
  }

  91.3% {
    transform: translate(0%);
  }
}

@keyframes square-step-3 {
  16.6% {
    transform: translate(0%);
  }

  24.9% {
    transform: translate(0%, -100%);
  }

  41.5% {
    transform: translate(0%, -100%);
  }

  49.8% {
    transform: translate(100%, -100%);
  }

  66.4% {
    transform: translate(100%, -100%);
  }

  74.7% {
    transform: translate(100%);
  }

  91.3% {
    transform: translate(100%);
  }

  100% {
    transform: translate(0%);
  }
}

